.just-for-dev {
  position: absolute;
  right: 0;
}

* {
  font-size: 12px;
  font-weight: normal;
  font-family: 'Helvetica Neue', Helvetica, Arial sans-serif;
}

h1,
h2 {
  font-size: 12px;
}

html {
  background: #fcfcfc;
}

/*Grid setup*/
/*MAIN GRID*/
.App {
  position: relative;
  display: block;
  width: 951px;
  height: 700px;
}

label,
input[type='checkbox'],
input[type='radio'] {
  cursor: pointer;
}

.text {
  font-size: 9.6pt;
  line-height: 14pt;
}

/* Wizard
------------------------------------------------*/
h2.wizardTitle {
  font-family: Arial;
  font-size: 13pt;
  font-weight: bold;
  color: #225b33;
  margin-left: 5px;
  margin-bottom: 0px;
}

.wizardDescription {
  margin-bottom: 10px;
}
h1.welcome-title {
  font-family: Arial, sans-serif;
  font-size: 22pt;
  font-weight: bold;
  color: black;
}

.welcome-img {
  width: 951px;
  height: 153px;
  margin-top: 6px;
  margin-bottom: 54px;
}

h2.district-ranking-title {
  font-family: Arial, sans-serif;
  font-size: 13pt;
  font-weight: bold;
  color: black;
}

div.hideDescription {
  display: none;
}

/* Main view
-----------------------------------------------*/
.main-view {
  position: relative;
}

.main-view > .information {
  height: 10%;
  top: 0;
}

.main-view > .title {
  height: 5%;
  top: 10%;
}

.main-view > .district-ranking {
  height: 83%;
  top: 120px /**--ranking-top**/;
}

.main-view > .indicator-ranking {
  height: 95%;
  top: 0;
}

.main-view > .navigation {
  height: 4%;
  bottom: 0;

  text-align: center;
  display: flex;
}

.main-view > .left-grid {
  position: absolute;
  width: 300px; /*--district-ranking-width*/
  left: 0;
}

.main-view > .right-grid {
  position: absolute;
  width: calc(100% - 300px /*--district-ranking-width*/);
  left: 300px; /* --district-ranking-width */
}


.district-ranking > .container {
  --margin: 4%;
  position: relative;
  display: inline-table;
  width: calc(100% - 4px /**--margin**/);
  height: calc(100% - 4px /**--margin**/);
  left: calc(4px /**--margin**/ / 2);
  background: #f5f5f5;
  border: 1px solid #cccccc;
  border-radius: 5px;
  overflow: hidden;
}
.mainFrame {
  background-color: #f5f5f5;
  border: 1px solid #cccccc;
  border-radius: 2px;
}

/*----------------------------------------------*/
/* INDICATOR SELECTION                          */
/*----------------------------------------------*/
.selections {
  display: flex;
}

.selectionGroup {
  display: flex; /* This pushs the toggle group to the bottom */
  flex-direction: column;
  justify-content: flex-start;
  flex: 1 1; /* All columns same width */
  background-color: #fcfcfc;
  margin: 5px;
  padding-left: 5px;
  border: 1px solid #cccccc;
  border-radius: 2px;
}

.selectionGroupTitle {
  padding: 5px 0px 3px 5px;
}
.selectionCheckboxes {
  flex-grow: 1;
  padding-bottom: 10px;
}

.selectionCheckbox {
  padding-bottom: 2px;
}

.toggleGroup {
  padding: 0px 0px 5px 0px;
}

/*RANKING MAIN CONTAINER
-----------------------------------------------*/
.ranking-container {
  position: absolute;
  overflow: visible;
  height: 590px;
  width: 31%;
  top: 172px;
  background: #fcfcfc;
}

.line-ranking {
  position: relative;
  /* background: blue;*/
  height: 100%;
  width: 100%;
  z-index: 2;
}

.legend-container {
  align-items: center;
  display: -webkit-box;
  width: 190px;
  height: 110px;

  margin-left: 3px; /**--column-margin-indicator**/
  margin-right: 3px; /**--column-margin-indicator**/

  border-radius: 5px;
  text-align: center;
  border: 1px solid #999999;

  /* background: #cc8dab; */
}

.neutral-border {
  border: 1px solid #aaa;
}

.legend-container-title {
  display: flex;
  flex-direction: column;
}

.remove {
  align-items: right;
  grid-area: remove;
  border-top-right-radius: 4px;
}

.legend-container-left {
  width: 50px;
  display: flex;
  flex-direction: column;
}

/*RANKING MAIN CONTAINER -----------------------------------------------*/

.legend-container-right {
  width: 165px;
  display: flex;
  justify-content: flex-end;
}

.legend-name {
  text-align: left;
  border-top-left-radius: 4px;
  padding-left: 10px;
  padding-top: 5px;
  /* background: cornflowerblue; */
}

.rate-positive {
  grid-area: positiv;
  align-items: center;
  /* background: #0044ed; */
}

.rate-negativ {
  grid-area: negativ;
  align-items: center;

  /* background: #eded00; */
}

.rating-icon {
  color: white;
  border-radius: 0 12px 12px 0;
  width: 100% !important;
}

.weight-label {
  grid-area: weighting;
  margin-top: 12px;
  /* background: cornflowerblue; */
}

.weight {
  padding: 0 20px;
  margin-top: 10px;
  text-align: left;
  color: #808080;
}

/* Indicator selection and deselection
------------------------------------------------*/

/*------------------------------------------------*/
/* R A I T I N G  (Pos / Neg)                     */
/*------------------------------------------------*/
.contentBox {
  background-color: #fcfcfc;
  margin: 5px;
  padding: 8px 5px 8px 5px;
  padding-right: 5px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  height: 100%;
}

.rating {
  display: flex;
}

.ratingLeft {
  flex: 8 1;
  background-color: #fcfcfc;
  margin: 5px;
  padding: 8px 5px 8px 5px;
  padding-right: 5px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  height: 100%;
}

.raitingRight {
  flex: 4 1;
  background-color: #fcfcfc;
  margin: 5px;
  padding: 8px 5px 8px 5px;
  padding-right: 5px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  height: 100%;
}

.indicatorRatingDescription {
  padding: 4px 0px 4px 0px;
  height: 45px;
}
.ratingLine {
  /* Use Flex row system */
  display: flex;
  padding: 4px 0px 4px 0px;
  height: 45px;
  justify-content: space-between;
}

.ratingLineButtons {
  display: flex;
  height: 35px;
  justify-content: flex-end;
}

.ratingButton {
  display: flex;
  color: white;
  text-transform: none;
  border-radius: 2px;
  align-items: center;
  box-shadow: 1px 2px #c1c1c1;
}

/* Rating buttons
------------------------------------------------*/
.negative-selected {
  background-color: #e54803;
  border: 1px solid #e54803;
}

.negative-unselected {
  background-color: #e6d3cb;
  border: 1px solid #e6d3cb;
  cursor: pointer;
}

.negative-unselected-disabled {
  background-color: #e6d3cb;
  border: 1px solid #e6d3cb;
  cursor: not-allowed;
}

.negative-unselected:hover {
  background-color: #e54803;
  border: 1px solid #e54803;
}

.negative {
  background: #e54803;
  border: 1px solid #e54803;
}

.positive-selected {
  background-color: #0387c1;
  border: 1px solid #0387c1;
  margin-right: 10px;
}

.positive-unselected {
  background-color: #9ab6c3;
  border: 1px solid #9ab6c3;
  margin-right: 10px;
  cursor: pointer;
}

.positive-unselected-disabled {
  background-color: #9ab6c3;
  border: 1px solid #9ab6c3;
  margin-right: 10px;
  cursor: not-allowed;
}

.positive-unselected:hover {
  background-color: #0387c1;
  border: 1px solid #0387c1;
  margin-right: 10px;
}

/* Remove horizontal lines in barchart */
.recharts-wrapper .recharts-cartesian-grid-horizontal {
  stroke-opacity: 0;
}

/* keep white space in labels of y-axis */
.preserveWhiteSpace {
  white-space: pre;
  margin-left: 1em;
}

.bold {
  font-weight: bold;
}

.recharts-cartesian-axis-tick-value {
  fill: black;
  white-space: pre;
}

.clear-icon {
  cursor: pointer;
}

.disclaimer {
    display: flex;
    border: 1px solid #cccccc;
    border-radius: 5px;
    padding: 5px;
    background-color: #f5f5f5;
    justify-content: space-between;
    margin: 0px 5px 5px 5px;
}

.disclaimerCloseButton {
    border:none;
    background-color: #f5f5f5;
    cursor: pointer;
}

.main-view > .disclaimer-main-view {
  top: 102%;
  display: flex;
  width: 631px !important;
  left: 300px !important;
}

.tooltip {
    position: relative;
  }
  .tooltip-trigger {
    /* display: inline-block; */
    cursor: help;
  }
  .tooltip-bubble {
    position: absolute;
    display: inline-block;
    z-index: 10;
    width: 350px;
    
  }
  .tooltip-top {
      bottom: 100%;
      left: 150px;
      padding-bottom: 9px;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
    }
  .tooltip-bubble::after {
    content: '';
    position: absolute;
    }
    
  .tooltip-top::after {
    border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    border-top: 9px solid #fff;
    bottom: 0;
    left: 10%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .tooltip-message {
    background: #fff;
    border-radius: 3px;
    color: #000;
    font-size: 1rem;
    line-height: 1.4;
    padding: 0.75em;
    text-align: left;
    box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.1), 10px 6px 20px 0 rgba(0, 0, 0, 0.19);
  }


.districtRankingExplanation {
    margin-left: 5px;
}

.districtRankingExplanation ul {
    display: table;
    margin: 0;
    padding: 0;
}

.districtRankingExplanation li {
    list-style: none;
    display: table-row;
}

.districtRankingExplanation li::before {
    display: table-cell;
    content: "-";
    text-align: right;
    padding-right: .3em;
}
.nav-button {
  display: flex !important;
  /* Normally the width would be 190px, but with these values it looks most correct  */
  width: 191px;
  margin-left: 0.5% !important;
  margin-right: 0.6% !important;
}

div.wizardFooter {
  margin: 10px 5px 5px 5px;
  height: 80px;
}

p.stepper-description {
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: 30px;
  margin-right: 30px;
}

.wizard-right-button {
  width: 100% !important;
  margin-bottom: 5px !important;
  padding: 8px 13px !important;
}

.wizard-left-button {
  width: 100% !important;
  margin-bottom: 5px !important;
  padding: 8px 13px !important;
}

.legend-select {
  width: 156px;
  height: 23px;
  border-radius: 0px 3px 3px 0px;
  border: 0.5px solid #999999;
  margin-right: 2px;
  margin-top: 2px;
  overflow: hidden;
}
.legend-select-empty {
  height: 24px;
  width: 186px;
  margin-left: 2px;
  margin-top: 2px;
  border-radius: 3px 3px 3px 3px;
  border: 0.5px solid #999999;
}

.clear-icon {
  display: flex;
  font-size: 12px !important;
}

.clear-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 30px;
  height: 23px;
  margin-left: 2px;
  margin-top: 2px;

  border-radius: 3px 0px 0px 3px;
  border: 0.5px solid #999999;
  background: #9999992e;
}

.clear-icon-container:hover {
  background-color: hsl(0, 90%, 95%);
  cursor: pointer;
}

.weight-label {
  color: black;
}
#scroll-area {
  --number-of-selected-indicators: 0; /* the default */
}

.parallel-line-plot-scroll-area {
  position: absolute;
  width: 100%;
  height: 100%;

  overflow-x: scroll;
  overflow-y: hidden;
  /* background: green; */
}

.parallel-line-plot-container {
  --flex-box-margin-multiplier: 4;
  height: 100%;
}

.parallel-line-plot-legend {
  height: 18% /**--parallel-line-plot-height:**/ ;
  width: inherit;
  /* background: rgb(245, 143, 0); */
  align-items: center;
  display: flex;

  /* rechars creates some transparant divs...*/
}

.parallel-line-plot-chart {
  position: absolute;
  /* background: cornflowerblue; */
  left: calc(190px /**--column-width-indicator:**/ / 2);
  top: 125px;
}

/* Height need to be aligned with container (of rankning) */
.bar-plot-container {
  height: calc(100% - 18% /**--parallel-line-plot-height:**/ - 1% /**scroll bar**/);
  width: inherit;
  top: 120px /*--ranking-top*/;
  /* background: rgb(245, 143, 0); */
  align-items: center;
  position: absolute;
  display: inline-flex;
  overflow: hidden; /** hide recarts bar plot overflow (IE11)**/
}
/* For responsive container of recharts a width and height is needed */
.bar-plot {
  width: 190px /**--column-width-indicator:**/;

  /* The height must be set a little bit less than 100%,
     since the parent container otherwise hides the border.
   */
  height: 99.7%;
  margin-left: 3px; /**--column-margin-indicator**/
  margin-right: 3px; /**--column-margin-indicator**/
  border: 1px solid #999999;
  background-color: #ffffff;
}

.bar-plot-empty {
  border: 1px solid #aaa;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, Arial sans-serif;
}

